import React, { useState } from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";

import CardContent from "@material-ui/core/CardContent";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";

import AttachMoneyIcon from "@material-ui/icons/AttachMoney";
import QueryBuilderIcon from "@material-ui/icons/QueryBuilder";
import DateRangeIcon from "@material-ui/icons/DateRange";
import RoomOutlinedIcon from "@material-ui/icons/RoomOutlined";
import SearchIcon from "@material-ui/icons/Search";
import TouchAppOutlinedIcon from "@material-ui/icons/TouchAppOutlined";

import PurpleCard from "../PurpleCard";

import IconWithText from "../IconWithText";
import GreenButton from "../GreenButton";

import db from "../../db.json";

const jobs = db.jobs;

const useStyles = makeStyles((theme) => ({
  link: {
    textDecoration: "none",
  },
  cursor: {
    cursor: "inherit",
  },
  title: {
    fontSize: 18,
    fontWeight: theme.typography.fontWeightBold,
  },
  status: {
    fontSize: 16,
    fontWeight: theme.typography.fontWeightBold,
  },
  titleContainer: {
    marginBottom: theme.spacing(2),
  },
  yellow: {
    color: "#D7A200",
  },
  center: {
    textAlign: "center",
  },
  green: {
    color: theme.palette.success.main,
  },
}));

export default function JobPreview({ job, tabIndex }) {
  const classes = useStyles();

  const [jobFields, setJobFields] = useState({ ...job });

  const {
    jobId,
    title,
    description,
    salary,
    commitment,
    period,
    location,
    vacancies,
    applications,
    hirer,
    status,
  } = jobFields;

  const isExist = jobs.findIndex((job) => job.id === jobId) > -1;

  return (
    <Link
      to={isExist ? `/jobs/${jobId}` : undefined}
      className={clsx(classes.link, !isExist && classes.cursor)}
    >
      <PurpleCard className={classes.root}>
        <CardContent>
          <Grid container spacing={2}>
            <Grid item xs={8}>
              <Grid
                item
                container
                wrap="nowrap"
                alignItems="center"
                justify="space-between"
                className={classes.titleContainer}
              >
                <Grid item>
                  <Typography className={classes.title} gutterBottom>
                    {title}{" "}
                    <Typography component="span"> for {hirer.name}</Typography>
                  </Typography>
                </Grid>
              </Grid>

              <Grid container spacing={2} direction="column">
                <Grid item>
                  <Typography variant="body2" component="p" gutterBottom>
                    {description}
                  </Typography>
                </Grid>
                <Grid item>
                  <Grid container wrap="nowrap" spacing={1}>
                    <Grid item xs={4}>
                      <IconWithText
                        variant="body2"
                        IconComponent={<AttachMoneyIcon />}
                      >
                        {`Salary: ${salary}`}
                      </IconWithText>
                    </Grid>
                    <Grid item xs={4}>
                      <IconWithText
                        variant="body2"
                        IconComponent={<QueryBuilderIcon />}
                      >
                        {`Commitment: ${commitment}`}
                      </IconWithText>
                    </Grid>
                    {!status.includes("Completed") && (
                      <Grid item xs={4}>
                        <IconWithText
                          variant="body2"
                          IconComponent={<SearchIcon />}
                        >
                          {`Vacancies: ${vacancies}`}
                        </IconWithText>
                      </Grid>
                    )}
                  </Grid>
                  <Grid container wrap="nowrap" spacing={1}>
                    <Grid item xs={4}>
                      <IconWithText
                        variant="body2"
                        IconComponent={<DateRangeIcon />}
                      >
                        {`Period: ${period}`}
                      </IconWithText>
                    </Grid>
                    <Grid item xs={4}>
                      <IconWithText
                        variant="body2"
                        IconComponent={<RoomOutlinedIcon />}
                      >
                        {`Location: ${location}`}
                      </IconWithText>
                    </Grid>
                    {!status.includes("Completed") && (
                      <Grid item xs={4}>
                        <IconWithText
                          variant="body2"
                          IconComponent={<TouchAppOutlinedIcon />}
                        >
                          {`Applications: ${applications}`}
                        </IconWithText>
                      </Grid>
                    )}
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
            <Grid container item xs={4}>
              <Grid xs={12} item>
                {tabIndex !== 2 && (
                  <Typography className={classes.status} gutterBottom>
                    {"Status: "}
                    <span
                      className={clsx(
                        tabIndex === 0
                          ? classes.yellow
                          : tabIndex === 1
                          ? classes.green
                          : undefined
                      )}
                    >
                      {status}
                    </span>
                  </Typography>
                )}
              </Grid>
              <Grid xs={12} item className={classes.center}>
                <ApplicationAction
                  status={status}
                  tabIndex={tabIndex}
                  setJobFields={setJobFields}
                />
              </Grid>
            </Grid>
          </Grid>
        </CardContent>
      </PurpleCard>
    </Link>
  );
}

function ApplicationAction({ status, tabIndex, setJobFields }) {
  const classes = useStyles();

  if (tabIndex === 2) {
    return <Typography className={classes.title}>{status}</Typography>;
  }

  switch (status) {
    case "Pending interview":
      return (
        <GreenButton
          variant="contained"
          onClick={(e) => {
            e.preventDefault();
            setJobFields((jobFields) => ({
              ...jobFields,
              status: "Interview Scheduled",
            }));
          }}
        >
          Schedule Interview
        </GreenButton>
      );
    case "Offered":
      return (
        <GreenButton
          variant="contained"
          onClick={(e) => {
            e.preventDefault();
            setJobFields((jobFields) => ({ ...jobFields, status: "Accepted" }));
          }}
        >
          Accept Offer
        </GreenButton>
      );
    case "Accepted":
      return (
        <Typography className={clsx(classes.title, classes.green)}>
          Accepted
        </Typography>
      );
    default:
      return null;
  }
}
